#mode-controls-container {
    display: flex;
    flex-direction: row;
    align-items: center;
    gap: 1.5rem;
    background-color: var(--primary-color-dark);
    border-radius: 1rem;
    padding: 1rem;

    h2 {
        color: var(--contrasting-color);
        font-family: var(--font-main);
        font-weight: 300;
        font-size: 1rem;
    }
}

.selection-button {
    font-family: var(--font-main);
    background: none;
    border: 1px solid var(--contrasting-color);
    border-radius: 0.5rem;
    color: var(--color-white);
    padding: 0.5rem 1rem;
}

.selection-button-selected {
    font-family: var(--font-main);
    background: none;
    border: 1px solid #ffffff00;
    border-radius: 0.5rem;
    color: var(--color-white);
    padding: 0.5rem 1rem;
}